*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 1.5rem;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}

main {
  display: flex;
  justify-content: center;
  padding: 1rem;
  background-color: #ffffff;
  border-radius: 15px;
  max-width: 1200px;
  margin: 0 auto;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  text-transform: uppercase;
  color: #000000;
}